<template>
    <div class="w-100 bg-white custom-chart-div t-min-height Barchrt text-center"
        :style="'height: ' + outerDivHeight + 'px;'">
        <div class="graph-widget-content bg-white float-left w-100" :style="'height: ' + outerDivHeight + 'px;'">
            <div v-if="isLoading" class="justify-content-start"
                :style="'height: ' + outerDivHeight + 'px;display: flex;align-items: center;justify-content: center !important;font-size: 3em;color: #CCC;'">
                <i class="fa fa-circle-o-notch fa-spin"></i>
            </div>
            <template v-if="BarWidgetData.length === 0 && isLoading === false">
                <div class="text-danger no-record"
                    :id="'BarChart_' + moduleCode + '_' + action.replace(/\s+/g, '_') + index"
                    :style="{ height: outerDivHeight + 'px' }"
                    style="display: flex; align-items: center; justify-content: center">
                    {{ $t("NoRecordfound") }}
                </div>
            </template>
            <template v-else>
                <!-- {{ height }} {{resizeheight * 35}} {{ initialColor }}-->
                <div :id="'BarChart_' + moduleCode + '_' + action.replace(/\s+/g, '_') + index"
                    :style="'height: ' + outerDivHeight + 'px;'">

                    <VueApexCharts type="bar" height="100%" :options="chartOptions" :series="series"></VueApexCharts>

                </div>
            </template>
        </div>
    </div>
</template>

<script>
import DataService from "../../../services/DataService";
import VueApexCharts from "vue-apexcharts";
var incrementedIndexForBar = 0
export default {
    components: {
        VueApexCharts
    },
    props: {
        reportName: null,
        chartType: String, // Set the type
        // selectedChartType: String,
        dashboardid: Number,
        moduleCode: null,
        fromDate: "",
        toDate: "",
        filterData: "",
        index: null,
        reportId: null,
        isSystemReport: null,
        width: null,
        height: null,
        resizeheight: null,
        xaxisAliasName: null,
        yaxisAliasName: null
    },
    data() {
        return {
            isLoading: true,
            action: this.reportName,
            charT_TYPE: "bar",
            xaxis: "",
            yaxis: "",
            BarWidgetData: [],
            HeaderData: null,
            showAddDashboardReport: false,
            chart: null, // Add a chart property to store the C3.js chart instance
            outerDivHeight: 0,
            data: [],
            categories: [],
            chartOptions: {},
            series: [],
            chartOptionXAxisData: [],
            chartSeriesData: [],
            selectedColors: ["#00E396", "#FF4560", "#775DD0", "#FEB019"],
            initialColor: '',

        };
    },
    created() {
        this.FetchData();
        this.initialColor = this.getRandomColor();
    },
    watch: {
        width: 'handleResize',
        height: 'handleResize',
        toDate: 'handleFetchData',
        fromDate: 'handleFetchData',
        filterData: 'handleFetchData',
        chartType: 'handleFetchData',
        resizeheight: function () {

            var h = document.getElementById('BarChart_' + this.moduleCode + '_' + this.action.replace(/\s+/g, '_') + this.index)
            //this.outerDivHeight = h.parentElement.parentElement.parentElement.offsetHeight - 100;
            if (h != null) {
                this.outerDivHeight = h.parentElement.parentElement.parentElement.offsetHeight - 100;
            }

        }
    },
    methods: {
        getRandomColor() {
            incrementedIndexForBar++;
            var ind = (incrementedIndexForBar % 4);
            return this.selectedColors[ind];
        },
        ViewReport(reportId) {
            const encodedReportID = btoa(JSON.stringify({ ReportID: reportId }));
            const route = '/Report/viewcustomreport/' + encodedReportID;
            // Open the route in a new tab
            window.open(route, '_blank');
        },
        async FetchData() {
            var vueObj = this;
            vueObj.isLoading = true;
            var reportidcheck = vueObj.reportId == undefined ? 0 : vueObj.reportId;
            var params = `moduleName=${vueObj.moduleCode}&currentChartType=${vueObj.charT_TYPE}&dashboardId=${vueObj.dashboardid}&reportName=${vueObj.reportName}&fromDate=${vueObj.fromDate}&toDate=${vueObj.toDate}&ReportId=${reportidcheck}&SearchFilter=${vueObj.filterData}`;
            await DataService.GetDashboardData(params).then(function (response) {
                vueObj.$emit("loaded");
                if (response.data != null && response.data != "") {
                    if (response.data.status == "Success") {
                        if (response.data.result != null) {
                            vueObj.BarWidgetData = response.data.result.data;
                            vueObj.xaxis = response.data.result.Xaxis;
                            vueObj.yaxis = response.data.result.Yaxis;
                            vueObj.bindWidget(vueObj);
                        }
                    }
                }
                var h = document.getElementById('BarChart_' + vueObj.moduleCode + '_' + vueObj.action.replace(/\s+/g, '_') + vueObj.index);
                if (h != null) {
                    vueObj.outerDivHeight = h.parentElement.parentElement.parentElement.offsetHeight - 100;
                }
                //vueObj.outerDivHeight = h.parentElement.parentElement.parentElement.offsetHeight - 100;
                vueObj.isLoading = false;
            });
        },
        bindWidget(vueObj) {

            var vm = this;

            if (vueObj.charT_TYPE && vueObj.BarWidgetData.length > 0) {

                var multiBars = []
                if (vm.chartType != "multiple_bar" && vm.chartType != "stacked_bar") {
                    vm.data = [];
                    vm.categories = [];

                    $.each(vueObj.BarWidgetData, function (index, element) {

                        if (element.VALUE !== undefined) {
                            vm.data.push(element.VALUE);
                            vm.categories.push(element.NAME);
                        }
                    });

                    vm.series = [{
                        name: vueObj.xaxisAliasName,
                        data: vm.data
                    }];
                    vm.chartOptions = {
                        // annotations: {
                        //     points: [{
                        //         //x: 'Bananas',
                        //         //seriesIndex: 0,
                        //         label: {
                        //             //borderColor: '#775DD0',
                        //             offsetY: 0,
                        //             // style: {
                        //             //     color: '#fff',
                        //             //     background: '#775DD0',
                        //             // },
                        //             //text: 'Bananas are good',
                        //         }
                        //     }]
                        // },
                        chart: {
                            // height: 350,
                            type: 'bar',
                        },
                        plotOptions: {
                            bar: {

                                dataLabels: {
                                    position: 'top',
                                },
                                borderRadius: 0,
                                columnWidth: '30px',
                                horizontal: (vueObj.chartType.toLowerCase() === "bar" || vueObj.chartType.toLowerCase() === "multiple_bar") ? false : true,
                            }
                        },
                        dataLabels: this.chartType.toLowerCase() === "horizontal_bar" ? {
                            enabled: true,
                            position: 'top',
                            style: {
                                colors: ['#000'],
                            }
                        } : {
                            enabled: true,
                            formatter: function (val) {
                                return val;
                            },
                            offsetY: -20,
                            style: {
                                fontSize: '15px',
                                colors: ["#304758"]

                            }
                        },
                        // dataLabels: {
                        //     enabled: vueObj.chartType.toLowerCase() !== "horizontal_bar" ? true : false,
                        //     formatter: function (val) {
                        //         return val;
                        //     },
                        //     offsetY: -20,
                        //     style: {
                        //         fontSize: '15px',
                        //         colors: ["#304758"]

                        //     }
                        // },

                        stroke: {
                            width: 0
                        },
                        grid: {
                            row: {
                                colors: ['#fff', '#f2f2f2']
                            }
                        },
                        colors: [vm.initialColor],
                        xaxis: {
                            labels: {
                                rotate: -45
                            },
                            categories: vm.categories,
                            tickPlacement: 'on',
                            title: {
                                text: vueObj.chartType.toLowerCase() === "bar" ? vm.$t(vm.xaxisAliasName) : vm.$t(vm.yaxisAliasName),
                            }
                        },
                        yaxis: {
                            title: {
                                text: vueObj.chartType.toLowerCase() === "bar" ? vm.$t(vm.yaxisAliasName) : vm.$t(vm.xaxisAliasName),

                            },
                            labels: {
                                trim: true
                            },
                        },

                        // fill: {
                        //     type: 'gradient',
                        //         gradient: {
                        //         shade: 'light',
                        //         type: "horizontal",
                        //         shadeIntensity: 0.25,
                        //         gradientToColors: undefined,
                        //         inverseColors: true,
                        //         opacityFrom: 0.85,
                        //         opacityTo: 0.85,
                        //         stops: [50, 0, 100]
                        //     },
                        // }

                    }
                }
                else {

                    var multibarsData = [];
                    var keys = [];

                    vm.categories = [];
                    vm.chartOptionXAxisData = [];
                    vm.chartSeriesData = [];
                    vm.series = [];

                    vm.BarWidgetData.forEach((mitem, mainindex) => {

                        vm.categories.push(mitem.NAME);

                        var obj = {
                            XaxisField: mitem['NAME']
                        }
                        vm.chartOptionXAxisData.push(mitem['NAME'])
                        mitem.Data.forEach(item => {
                            var pushitem = item['YFieldData'] != undefined ? item['YFieldData'] : '-';
                            var checkexist = keys.filter(x => x == pushitem);
                            obj[pushitem] = item.Countable;
                            if (checkexist.length == 0) {
                                keys.push(pushitem);
                            }
                        });
                        multibarsData.push(obj);
                    });
                    keys.forEach(function (x) {
                        var obj = {
                            name: x,
                            data: []
                        };
                        multibarsData.forEach(function (y) {
                            if (y[x]) {
                                obj.data.push(y[x]);
                            } else {
                                obj.data.push(0);
                            }
                        });
                        vm.chartSeriesData.push(obj);
                    });

                    if (vm.chartType == "multiple_bar") {
                        vm.series = vm.chartSeriesData,

                            vm.chartOptions = {
                                chart: {
                                    type: 'bar',
                                    //height: 350
                                },
                                plotOptions: {
                                    bar: {
                                        horizontal: false,
                                        columnWidth: '30px',
                                        endingShape: 'rounded'
                                    },
                                },
                                dataLabels: {
                                    enabled: false
                                },
                                stroke: {
                                    show: true,
                                    width: 2,
                                    colors: ['transparent']
                                },
                                xaxis: {
                                    categories: vm.categories,
                                    title: {
                                        text: vm.$t(vm.xaxisAliasName),
                                    }
                                },
                                yaxis: {
                                    title: {
                                        text: vm.$t(vm.yaxisAliasName),
                                    }
                                },
                                fill: {
                                    opacity: 1
                                },
                                tooltip: {
                                    y: {
                                        formatter: function (val) {
                                            return val
                                        }
                                    }
                                },
                            }
                    }
                    else {

                        vm.series = vm.chartSeriesData,

                            vm.chartOptions = {
                                chart: {
                                    type: 'bar',
                                    height: 350,
                                    stacked: true,
                                    toolbar: {
                                        show: true
                                    },
                                    zoom: {
                                        enabled: true
                                    }
                                },
                                responsive: [{
                                    breakpoint: 480,
                                    options: {
                                        legend: {
                                            position: 'bottom',
                                            offsetX: -10,
                                            offsetY: 0
                                        }
                                    }
                                }],
                                plotOptions: {
                                    bar: {
                                        horizontal: false,
                                        columnWidth: '25%',
                                        borderRadius: 0,
                                        borderRadiusApplication: 'end', // 'around', 'end'
                                        borderRadiusWhenStacked: 'last', // 'all', 'last'
                                        dataLabels: {
                                            total: {
                                                enabled: true,
                                                style: {
                                                    fontSize: '13px',
                                                    fontWeight: 900
                                                }
                                            }
                                        }
                                    },
                                },
                                xaxis: {
                                    //type: 'datetime',
                                    categories: vm.categories,
                                },
                                legend: {
                                    position: 'top',
                                    horizontalAlign:'left',
                                    offsetY: 10
                                },
                                fill: {
                                    opacity: 1
                                }
                            };
                    }


                }
            }
        },
        handleResize() {
            this.bindWidget(this);
        },
        handleFetchData() {
            this.FetchData();
        }
    },
};
</script>
