<template>
    <li @click="!isInEditMode && ViewNumericReport(reportId, Access)" class="cursor-pointer one_wid position-relative"
        style="cursor: pointer !important; display: inline-grid;"
        :style="{ borderColor: colorCode + ' !important', background: colorCode + '11 !important' }">
        <i :class="{ 'd-none': !isInEditMode, }" @click="removeItem(index, reportName)" aria-hidden="true"
            class="fa fa-times mr-2 font-18" style="position: absolute; right: 0; top: 2px"></i>
           
        <template v-if="NumberWidgetData && NumberWidgetData.length > 0">
            <div class="d-flex justify-content-between" v-for="(item, i) in NumberWidgetData" :key="i">
                <h3 :style="{ color: colorCode + ' !important' }">{{ formatKPI(item.VALUE) }}</h3>
                <div>
                    <i aria-hidden="true" :class="icon ? icon : 'fa fa fa-list-ul'"></i>
                </div>
            </div>
        </template>
        <template v-else>
            <div class="d-flex justify-content-start">
                <i class="fa fa-circle-o-notch fa-spin"></i>
            </div>
        </template>
        <div>{{ GetLocalizedValue(reportName) }}</div>
    </li>
</template>
<script>
import "d3";
import "c3";
import "c3/c3.min.css";
import DataService from "../../../services/DataService";
export default {
    props: {
        reportName: null,
        chartType: String, // Set the type
        dashboardid: Number,
        moduleCode: null,
        fromDate: "",
        toDate: "",
        index: null,
        icon: null,
        reportId: null,
        isSystemReport: null,
        numberLayout: null,
        isInEditMode: null,
        filterData: "",
        colorCode: null,
        Access:null

        // numberData: null,
    },
    data() {
        return {
            isLoading: true,
            action: this.reportName,
            charT_TYPE: "numbers_chart",
            NumberWidgetData: [],
            selectedColors: ["#23a5fd", "#37b8df", "#c49e71", "#488fa4", "#dbb429"],
            selectedBackgroundColors: ["#edf7ff", "#eef7f9", "#fff4e8", "#def5fa", "#fff8df"],
            initialColor: '',
            initialBackgroundColor: '',
            showAddDashboardReport: false,
            chart: null, // Add a chart property to store the C3.js chart instance
            NumericValue:0
        };
    },
    created() {

        this.FetchData();
        this.initialColor = this.getRandomColor();
        this.initialBackgroundColor = this.getRandomBackgroundColor();

    },
    watch: {
        numberLayout() {
            this.FetchData();
        }
    },
    methods: {
        removeItem(i) {

            var vm = this;
            vm.$emit('removeWidgets', i, vm.reportId, vm.chartType)
            // vm.$parent.removeItem(i, vm.reportId, vm.chartType);
        },
        getRandomColor() {
            const randomIndex = Math.floor(Math.random() * this.selectedColors.length);
            return this.selectedColors[randomIndex];
        },
        getRandomBackgroundColor() {
            const randomIndex = Math.floor(Math.random() * this.selectedBackgroundColors.length);
            return this.selectedBackgroundColors[randomIndex];
        },
        formatKPI(number) {
            if (number >= 1e9) {
                return (number / 1e9).toFixed(1) + 'B';
            } else if (number >= 1e6) {
                return (number / 1e6).toFixed(1) + 'M';
            } else if (number >= 1e3) {
                return (number / 1e3).toFixed(1) + 'K';
            } else {
                return number.toString();
            }
        },
        async FetchData() {
            var vueObj = this;
            vueObj.isLoading = true;
            vueObj.NumericValue=0;
            var reportidcheck = vueObj.reportId == undefined ? 0 : vueObj.reportId;
            var params = `moduleName=${vueObj.moduleCode}&currentChartType=${vueObj.charT_TYPE}&dashboardId=${vueObj.dashboardid}&reportName=${vueObj.reportName}&fromDate=${vueObj.fromDate}&toDate=${vueObj.toDate}&ReportId=${reportidcheck}&SearchFilter=${vueObj.filterData}`;
            await DataService.GetDashboardData(params).then(function (response) {
                vueObj.$emit("loaded");
                if (response.data.result != null && response.data.result != "") {
                    if (response.data.status == "Success") {

                        if (response.data.result.data != null) {

                            vueObj.NumberWidgetData = response.data.result.data;
                            vueObj.NumericValue = vueObj.NumberWidgetData[0]?.VALUE
                           
                        }
                    }
                }
                vueObj.isLoading = false;
            });
        },


        ViewNumericReport(reportId, Access) {  
            debugger                  
            if(this.NumericValue == 0)  
            return; 
            const encodedReportID = btoa(JSON.stringify({ ReportID: reportId, ACCESS: Access, FromDate:this.fromDate, ToDate: this.toDate  }));
            const route = '/Report/viewcustomreport/' + encodedReportID;
            // Open the route in a new tab
            window.open(route, '_blank');
        },
    },
};
</script>